import React, { useState } from "react";
import { Bar, Line, Pie } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  PointElement,
  LineElement,
  ArcElement
} from 'chart.js';
import { startOfWeek, format } from 'date-fns';
import { Link } from 'react-router-dom';
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend, PointElement, LineElement, ArcElement);

function Analysis({ products, history, darkMode }) {
  const [selectedMonth, setSelectedMonth] = useState('all');
  const [graphMode, setGraphMode] = useState('qty');
  const [showPrediction, setShowPrediction] = useState(true);
  const [stockTarget, setStockTarget] = useState('all'); // 'all' or product.id
  const [salesTarget, setSalesTarget] = useState('all'); // 'all' or product.id
  const [salesMode, setSalesMode] = useState('qty'); // 'qty' or 'total'

  // 月別集計データ作成
  const months = Array.from(new Set(history.map(h => h.date))).sort();
  const monthlyIn = months.map(m => history.filter(h => h.date === m && h.type === 'in').reduce((sum, h) => sum + h.qty, 0));
  const monthlyOut = months.map(m => history.filter(h => h.date === m && h.type === 'out').reduce((sum, h) => sum + h.qty, 0));
  const monthlyOutTotal = months.map(m => history.filter(h => h.date === m && h.type === 'out').reduce((sum, h) => sum + h.total, 0));

  // 月移動用関数
  const monthList = months;
  const currentMonthIndex = monthList.indexOf(selectedMonth);
  const prevMonth = currentMonthIndex > 0 ? monthList[currentMonthIndex - 1] : null;
  const nextMonth = currentMonthIndex >= 0 && currentMonthIndex < monthList.length - 1 ? monthList[currentMonthIndex + 1] : null;
  const filteredHistory = selectedMonth === 'all' ? history : history.filter(h => h.date === selectedMonth);

  // グラフ用オプション（ダークモード対応）
  const chartOptions = {
    plugins: {
      legend: {
        labels: {
          color: darkMode ? '#fff' : '#222',
        }
      },
      title: {
        color: darkMode ? '#fff' : '#222',
      },
      tooltip: {
        backgroundColor: darkMode ? '#222' : '#fff',
        titleColor: darkMode ? '#fff' : '#222',
        bodyColor: darkMode ? '#fff' : '#222',
      }
    },
    scales: {
      x: {
        ticks: { color: darkMode ? '#fff' : '#222' },
        grid: { color: darkMode ? '#555' : '#ccc' }
      },
      y: {
        ticks: { color: darkMode ? '#fff' : '#222' },
        grid: { color: darkMode ? '#555' : '#ccc' }
      }
    }
  };
  const darkColors = [
    '#ffb74d', '#4fc3f7', '#81c784', '#e57373', '#ba68c8', '#ffd54f', '#64b5f6', '#a1887f', '#90a4ae', '#f06292'
  ];
  const lightColors = [
    '#4CAF50', '#f44336', '#2196F3', '#FF9800', '#9C27B0', '#FFEB3B', '#00BCD4', '#E91E63', '#8BC34A', '#FFC107'
  ];
  const getColor = (i) => darkMode ? darkColors[i % darkColors.length] : lightColors[i % lightColors.length];

  // 週ごとに履歴を集計
  const getWeekKey = (dateStr) => {
    const d = new Date(dateStr + '-01');
    const weekStart = startOfWeek(d, { weekStartsOn: 1 }); // 月曜始まり
    return format(weekStart, 'yyyy-MM-dd');
  };
  const weekKeys = Array.from(new Set(history.map(h => getWeekKey(h.date)))).sort();
  // 商品ごとの週別販売数
  const weeklySales = products.map(p =>
    weekKeys.map(wk => history.filter(h => h.productId === p.id && h.type === 'out' && getWeekKey(h.date) === wk).reduce((sum, h) => sum + h.qty, 0))
  );
  // 商品ごとの直近4週平均販売数
  const avgSales4w = products.map((p, i) => {
    const sales = weeklySales[i].slice(-4);
    return sales.length ? sales.reduce((a, b) => a + b, 0) / sales.length : 0;
  });
  // 在庫切れ予測（あと何週で在庫0になるか）
  const weeksToOOS = products.map((p, i) => avgSales4w[i] > 0 ? p.stock / avgSales4w[i] : Infinity);
  // 発注アドバイス（2週以内に在庫切れ予測）
  const reorderAdvice = products.filter((p, i) => avgSales4w[i] > 0 && weeksToOOS[i] <= 2);

  // 週別グラフ用データ
  const weekLabels = showPrediction ? [...weekKeys, '予測'] : weekKeys;
  const salesData = [
    ...products.length ? weeklySales[0].map((_, i) => products.reduce((sum, p, pi) => sum + (weeklySales[pi][i] || 0), 0)) : []
  ];
  if (showPrediction && products.length) salesData.push(Math.round(products.reduce((sum, p, pi) => sum + avgSales4w[pi], 0)));
  const stockData = [
    ...weekKeys.map((_, i) => products.reduce((sum, p, pi) => sum + (p.stock - weeklySales[pi].slice(0, i + 1).reduce((a, b) => a + b, 0)), 0))
  ];
  if (showPrediction && products.length) stockData.push(products.reduce((sum, p, pi) => sum + (p.stock - avgSales4w[pi]), 0));

  // 予測バー/ポイントにAI注釈を出すカスタムツールチップ
  const chartOptionsWithPrediction = {
    ...chartOptions,
    plugins: {
      ...chartOptions.plugins,
      tooltip: {
        ...chartOptions.plugins.tooltip,
        callbacks: {
          ...((chartOptions.plugins.tooltip && chartOptions.plugins.tooltip.callbacks) || {}),
          title: (items) => {
            const item = items[0];
            if (item.label === '予測') return 'AI予測';
            return item.label;
          }
        }
      }
    }
  };

  // 在庫推移データ（月次）
  const stockLabels = months;
  const stockLineData = stockTarget === 'all'
    ? months.map(m => products.reduce((sum, p) => sum + (
        p.stock + history.filter(h => h.productId === p.id && h.date <= m && h.type === 'in').reduce((s, h) => s + h.qty, 0)
        - history.filter(h => h.productId === p.id && h.date <= m && h.type === 'out').reduce((s, h) => s + h.qty, 0)
      ), 0))
    : months.map(m => {
        const p = products.find(p => p.id === stockTarget);
        if (!p) return 0;
        return (
          p.stock + history.filter(h => h.productId === p.id && h.date <= m && h.type === 'in').reduce((s, h) => s + h.qty, 0)
          - history.filter(h => h.productId === p.id && h.date <= m && h.type === 'out').reduce((s, h) => s + h.qty, 0)
        );
      });

  // 売上推移データ（月次）
  const salesLabels = months;
  const salesBarData = salesTarget === 'all'
    ? months.map(m => history.filter(h => h.date === m && h.type === 'out').reduce((sum, h) => sum + (salesMode === 'qty' ? h.qty : h.total), 0))
    : months.map(m => history.filter(h => h.date === m && h.type === 'out' && h.productId === salesTarget).reduce((sum, h) => sum + (salesMode === 'qty' ? h.qty : h.total), 0));

  // カテゴリ別在庫割合データ
  const categoryStock = products.reduce((acc, p) => {
    acc[p.category] = (acc[p.category] || 0) + p.stock;
    return acc;
  }, {});
  const categoryLabels = Object.keys(categoryStock);
  const categoryData = Object.values(categoryStock);

  return (
    <div style={{ padding: 32, maxWidth: 900, margin: "0 auto", background: darkMode ? 'rgba(24,28,36,0.98)' : 'rgba(255,255,255,0.98)', borderRadius: 24, boxShadow: darkMode ? '0 4px 32px #10131a66' : '0 4px 32px #b0b8d866', color: darkMode ? '#fff' : '#222', transition: 'background 0.4s, color 0.4s' }}>
      <div style={{ marginBottom: 16 }}>
        <Link to="/home" style={{ padding: "8px 16px", background: darkMode ? '#388e3c' : '#388e3c', color: '#fff', textDecoration: "none", borderRadius: "8px", fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>
          在庫管理ページへ戻る
        </Link>
      </div>
      <h1 style={{ color: darkMode ? '#fff' : '#222', fontWeight: 900, letterSpacing: 2, fontSize: 32, marginBottom: 24, textShadow: darkMode ? '0 2px 8px #0006' : '0 2px 8px #fff8', transition: 'color 0.4s' }}>分析ページ</h1>
      {/* 発注アドバイス */}
      {reorderAdvice.length > 0 && (
        <div style={{ background: darkMode ? '#2d2f36' : '#f9fbe7', color: darkMode ? '#ffe082' : '#d84315', padding: 12, borderRadius: 8, marginBottom: 20, fontWeight: 'bold', transition: 'background 0.4s, color 0.4s' }}>
          <span>発注アドバイス：</span>
          {reorderAdvice.map((p) => (
            <span key={p.id} style={{ marginRight: 16 }}>
              「{p.name}」は約{Math.ceil(weeksToOOS[products.indexOf(p)])}週で在庫切れ予測
            </span>
          ))}
        </div>
      )}
      <section>
        <h2 style={{ color: darkMode ? '#fff' : '#222', fontWeight: 800, fontSize: 22, marginBottom: 16, transition: 'color 0.4s' }}>履歴・分析</h2>
        <div>
          <div style={{ marginBottom: 16 }}>
            <button onClick={() => setSelectedMonth(prevMonth)} disabled={!prevMonth} style={{ marginRight: 8, background: darkMode ? '#1976d2' : '#1976d2', color: '#fff', border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>前月</button>
            <select value={selectedMonth} onChange={e => setSelectedMonth(e.target.value)} style={{ marginRight: 8, background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, transition: 'background 0.4s, color 0.4s, border 0.4s' }}>
              <option value="all">すべての月</option>
              {months.map(m => (
                <option key={m} value={m}>{m}</option>
              ))}
            </select>
            <button onClick={() => setSelectedMonth(nextMonth)} disabled={!nextMonth} style={{ background: darkMode ? '#1976d2' : '#1976d2', color: '#fff', border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>翌月</button>
          </div>
          <div style={{ marginBottom: 16 }}>
            <span>グラフ表示：</span>
            <button onClick={() => setGraphMode('qty')} style={{ marginRight: 8, background: graphMode === 'qty' ? (darkMode ? '#4CAF50' : '#4CAF50') : (darkMode ? '#23273a' : '#eee'), color: graphMode === 'qty' ? '#fff' : (darkMode ? '#fff' : '#222'), border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>数量</button>
            <button onClick={() => setGraphMode('total')} style={{ background: graphMode === 'total' ? (darkMode ? '#4CAF50' : '#4CAF50') : (darkMode ? '#23273a' : '#eee'), color: graphMode === 'total' ? '#fff' : (darkMode ? '#fff' : '#222'), border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>金額</button>
          </div>
          <h2>月別仕入れ・販売グラフ</h2>
          <Bar
            data={{
              labels: selectedMonth === 'all' ? months : [selectedMonth],
              datasets: graphMode === 'qty'
                ? [
                    { label: '仕入れ数', data: selectedMonth === 'all' ? monthlyIn : [monthlyIn[months.indexOf(selectedMonth)]], backgroundColor: getColor(0) },
                    { label: '販売数', data: selectedMonth === 'all' ? monthlyOut : [monthlyOut[months.indexOf(selectedMonth)]], backgroundColor: getColor(1) }
                  ]
                : [
                    { label: '仕入れ金額', data: selectedMonth === 'all' ? months.map(m => history.filter(h => h.date === m && h.type === 'in').reduce((sum, h) => sum + h.total, 0)) : [history.filter(h => h.date === selectedMonth && h.type === 'in').reduce((sum, h) => sum + h.total, 0)], backgroundColor: getColor(0) },
                    { label: '販売金額', data: selectedMonth === 'all' ? monthlyOutTotal : [monthlyOutTotal[months.indexOf(selectedMonth)]], backgroundColor: getColor(1) }
                  ]
            }}
            options={chartOptions}
          />
          <h2 style={{ marginTop: 32 }}>商品別販売推移（折れ線）</h2>
          <Line
            data={{
              labels: selectedMonth === 'all' ? months : [selectedMonth],
              datasets: products.map((p, i) => ({
                label: p.name,
                data: graphMode === 'qty'
                  ? (selectedMonth === 'all'
                    ? months.map(m => history.filter(h => h.date === m && h.type === 'out' && h.productId === p.id).reduce((sum, h) => sum + h.qty, 0))
                    : [history.filter(h => h.date === selectedMonth && h.type === 'out' && h.productId === p.id).reduce((sum, h) => sum + h.qty, 0)])
                  : (selectedMonth === 'all'
                    ? months.map(m => history.filter(h => h.date === m && h.type === 'out' && h.productId === p.id).reduce((sum, h) => sum + h.total, 0))
                    : [history.filter(h => h.date === selectedMonth && h.type === 'out' && h.productId === p.id).reduce((sum, h) => sum + h.total, 0)]),
                borderColor: getColor(i),
                backgroundColor: getColor(i),
                fill: false,
                tension: 0.3,
                pointRadius: 3,
                pointBackgroundColor: getColor(i),
                pointBorderColor: getColor(i)
              }))
            }}
            options={chartOptions}
          />
          <h2 style={{ marginTop: 32 }}>月別売上金額（円グラフ）</h2>
          <Pie
            data={{
              labels: selectedMonth === 'all' ? months : [selectedMonth],
              datasets: [
                {
                  data: graphMode === 'qty'
                    ? (selectedMonth === 'all' ? monthlyOut : [monthlyOut[months.indexOf(selectedMonth)]])
                    : (selectedMonth === 'all' ? monthlyOutTotal : [monthlyOutTotal[months.indexOf(selectedMonth)]]),
                  backgroundColor: (selectedMonth === 'all' ? months : [selectedMonth]).map((_, i) => getColor(i))
                }
              ]
            }}
            options={chartOptions}
          />
          <h2 style={{ marginTop: 32 }}>履歴一覧</h2>
          <table border="1" cellPadding="6" style={{ width: '100%' }}>
            <thead>
              <tr>
                <th>ID</th><th>日付</th><th>商品名</th><th>種別</th><th>数量</th><th>単価</th><th>合計</th>
              </tr>
            </thead>
            <tbody>
              {filteredHistory.map(h => (
                <tr key={h.id}>
                  <td>{h.id}</td>
                  <td>{h.date}</td>
                  <td>{h.name}</td>
                  <td>{h.type === 'in' ? '仕入れ' : '販売'}</td>
                  <td>{h.qty}</td>
                  <td>{h.price}</td>
                  <td>{h.total}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </section>
      <section>
        <h2>週別売上・在庫推移（{showPrediction ? 'AI予測付き' : '実績のみ'}）</h2>
        <div style={{ marginBottom: 12, textAlign: 'right' }}>
          <label style={{ cursor: 'pointer', fontWeight: 'bold', fontSize: '0.95em' }}>
            <input type="checkbox" checked={showPrediction} onChange={e => setShowPrediction(e.target.checked)} style={{ marginRight: 6 }} />
            週別グラフにAI予測を表示
          </label>
        </div>
        <Bar
          data={{
            labels: weekLabels,
            datasets: [
              {
                label: '売上数',
                data: salesData,
                backgroundColor: getColor(0)
              },
              {
                label: '在庫合計',
                data: stockData,
                backgroundColor: getColor(1)
              }
            ]
          }}
          options={chartOptionsWithPrediction}
        />
      </section>
      <section>
        <h2 style={{ color: darkMode ? '#fff' : '#222', fontWeight: 800, fontSize: 22, marginBottom: 16, transition: 'color 0.4s' }}>在庫推移グラフ</h2>
        <div style={{ marginBottom: 16 }}>
          <label style={{ marginRight: 8 }}>表示対象：</label>
          <select value={stockTarget} onChange={e => setStockTarget(e.target.value)} style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, transition: 'background 0.4s, color 0.4s, border 0.4s' }}>
            <option value="all">全商品合計</option>
            {products.map(p => (
              <option key={p.id} value={p.id}>{p.name}</option>
            ))}
          </select>
        </div>
        <Line
          data={{
            labels: stockLabels,
            datasets: [
              {
                label: stockTarget === 'all' ? '全商品合計' : (products.find(p => p.id === stockTarget)?.name || ''),
                data: stockLineData,
                borderColor: getColor(0),
                backgroundColor: getColor(0),
                fill: false,
                tension: 0.3,
                pointRadius: 3,
                pointBackgroundColor: getColor(0),
                pointBorderColor: getColor(0)
              }
            ]
          }}
          options={chartOptions}
        />
      </section>
      <section>
        <h2 style={{ color: darkMode ? '#fff' : '#222', fontWeight: 800, fontSize: 22, marginBottom: 16, transition: 'color 0.4s' }}>売上推移グラフ</h2>
        <div style={{ marginBottom: 16, display: 'flex', alignItems: 'center', gap: 12 }}>
          <label style={{ marginRight: 8 }}>表示対象：</label>
          <select value={salesTarget} onChange={e => setSalesTarget(e.target.value)} style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, transition: 'background 0.4s, color 0.4s, border 0.4s' }}>
            <option value="all">全商品合計</option>
            {products.map(p => (
              <option key={p.id} value={p.id}>{p.name}</option>
            ))}
          </select>
          <button onClick={() => setSalesMode('qty')} style={{ background: salesMode === 'qty' ? (darkMode ? '#4CAF50' : '#4CAF50') : (darkMode ? '#23273a' : '#eee'), color: salesMode === 'qty' ? '#fff' : (darkMode ? '#fff' : '#222'), border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>売上数</button>
          <button onClick={() => setSalesMode('total')} style={{ background: salesMode === 'total' ? (darkMode ? '#4CAF50' : '#4CAF50') : (darkMode ? '#23273a' : '#eee'), color: salesMode === 'total' ? '#fff' : (darkMode ? '#fff' : '#222'), border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>売上金額</button>
        </div>
        <Bar
          data={{
            labels: salesLabels,
            datasets: [
              {
                label: salesTarget === 'all' ? '全商品合計' : (products.find(p => p.id === salesTarget)?.name || ''),
                data: salesBarData,
                backgroundColor: getColor(1),
                borderColor: getColor(1),
                borderWidth: 2,
                borderRadius: 6
              }
            ]
          }}
          options={chartOptions}
        />
      </section>
      <section style={{ background: darkMode ? '#23273a' : '#f7f7f7', borderRadius: 16, padding: 24, marginBottom: 32, boxShadow: darkMode ? '0 2px 8px #10131a33' : '0 2px 8px #b0b8d822', transition: 'background 0.4s' }}>
        <h2 style={{ color: darkMode ? '#fff' : '#222', fontWeight: 800, fontSize: 22, marginBottom: 16, transition: 'color 0.4s' }}>商品カテゴリ別在庫割合</h2>
        <Pie
          data={{
            labels: categoryLabels,
            datasets: [
              {
                data: categoryData,
                backgroundColor: categoryLabels.map((_, i) => getColor(i)),
                borderColor: darkMode ? '#181c24' : '#fff',
                borderWidth: 2
              }
            ]
          }}
          options={chartOptions}
        />
      </section>
    </div>
  );
}

export default Analysis; 