import React, { useState, useEffect } from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  PointElement,
  LineElement,
  ArcElement
} from 'chart.js';
import { startOfWeek, format } from 'date-fns';
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend, PointElement, LineElement, ArcElement);

const initialProducts = [
  { id: 1, name: "商品A", stock: 10, price: 1000, orderUnit: 1, costPrice: 700, sellPrice: 1200, category: "食品" },
  { id: 2, name: "商品B", stock: 5, price: 2000, orderUnit: 1, costPrice: 1500, sellPrice: 2500, category: "家電" },
];

// 初期値をlocalStorageから取得（アカウントごと）
const getInitialProducts = (username, dataset) => {
  const saved = localStorage.getItem(`products_${username}_${dataset}`);
  if (saved) return JSON.parse(saved);
  return initialProducts;
};
const getInitialHistory = (username, dataset) => {
  const saved = localStorage.getItem(`history_${username}_${dataset}`);
  if (saved) return JSON.parse(saved);
  return [];
};

function Home({ username, darkMode, searchTerm, exportToCSVRef, products, setProducts, history, setHistory }) {
  const [name, setName] = useState("");
  const [stock, setStock] = useState("");
  const [price, setPrice] = useState("");
  const [orderUnit, setOrderUnit] = useState("");
  const [costPrice, setCostPrice] = useState("");
  const [sellPrice, setSellPrice] = useState("");
  const [category, setCategory] = useState("");
  const [editId, setEditId] = useState(null);
  const [categories, setCategories] = useState(["食品", "家電"]);
  const [newCategory, setNewCategory] = useState("");
  const [sortConfig, setSortConfig] = useState({ key: null, direction: null });
  const [searchCategory, setSearchCategory] = useState("");
  const [categoryToDelete, setCategoryToDelete] = useState(null);
  const [productToDelete, setProductToDelete] = useState(null);
  const [showOrderModal, setShowOrderModal] = useState(false);
  const [orderProduct, setOrderProduct] = useState(null);
  const [orderDate, setOrderDate] = useState("");
  const [expectedDeliveryDate, setExpectedDeliveryDate] = useState("");
  const [orderQty, setOrderQty] = useState(1);
  const [orderHistory, setOrderHistory] = useState([]);
  const [showDeliveryModal, setShowDeliveryModal] = useState(false);
  const [deliveryOrder, setDeliveryOrder] = useState(null);
  const [actualDeliveryDate, setActualDeliveryDate] = useState("");
  const editFormRef = React.useRef(null);
  const rowRefs = React.useRef({});
  const [scrollToId, setScrollToId] = useState(null);

  // データセット管理用state
  const [datasets, setDatasets] = useState(() => {
    const saved = localStorage.getItem(`datasets_${username}`);
    return saved ? JSON.parse(saved) : ["メイン"];
  });
  const [currentDataset, setCurrentDataset] = useState(() => {
    const saved = localStorage.getItem(`currentDataset_${username}`);
    return saved ? saved : "メイン";
  });
  const [newDatasetName, setNewDatasetName] = useState("");

  // データセット切り替え時のproducts, historyの取得
  useEffect(() => {
    setProducts(getInitialProducts(username, currentDataset));
    setHistory(getInitialHistory(username, currentDataset));
    localStorage.setItem(`currentDataset_${username}`, currentDataset);
  }, [username, currentDataset]);

  // データセットリスト保存
  useEffect(() => {
    localStorage.setItem(`datasets_${username}`, JSON.stringify(datasets));
  }, [datasets, username]);

  // productsとhistoryの変更をlocalStorageに保存（アカウント・データセットごと）
  useEffect(() => {
    if (username && currentDataset) localStorage.setItem(`products_${username}_${currentDataset}`, JSON.stringify(products));
  }, [products, username, currentDataset]);
  useEffect(() => {
    if (username && currentDataset) localStorage.setItem(`history_${username}_${currentDataset}`, JSON.stringify(history));
  }, [history, username, currentDataset]);

  // body/htmlの背景色をダーク/ライトで切り替え、トランジションも付与
  useEffect(() => {
    const html = document.documentElement;
    const body = document.body;
    html.style.transition = 'background 0.4s';
    body.style.transition = 'background 0.4s';
    if (darkMode) {
      html.style.background = '#181c24';
      body.style.background = '#181c24';
    } else {
      html.style.background = 'linear-gradient(135deg, #e3e9f0 0%, #f8fafc 100%)';
      body.style.background = 'linear-gradient(135deg, #e3e9f0 0%, #f8fafc 100%)';
    }
    return () => {
      html.style.background = '';
      body.style.background = '';
      html.style.transition = '';
      body.style.transition = '';
    };
  }, [darkMode]);

  // 商品追加・更新
  const handleSubmit = (e) => {
    e.preventDefault();
    if (!name || stock === "" || price === "" || !category || stock < 0 || price < 0 || orderUnit === "" || orderUnit <= 0 || costPrice === "" || costPrice < 0 || sellPrice === "" || sellPrice < 0) {
      alert("すべての項目を正しく入力してください。");
      return;
    }
    const newProduct = {
      id: editId ?? (products.length > 0 ? Math.max(...products.map((p) => p.id)) + 1 : 1),
      name,
      stock: Number(stock),
      price: Number(price),
      orderUnit: Number(orderUnit),
      costPrice: Number(costPrice),
      sellPrice: Number(sellPrice),
      category,
    };
    if (editId === null) {
      setProducts([...products, newProduct]);
    } else {
      setProducts(products.map((p) => (p.id === editId ? newProduct : p)));
      setScrollToId(editId);
      setEditId(null);
    }
    setName("");
    setStock("");
    setPrice("");
    setOrderUnit("");
    setCostPrice("");
    setSellPrice("");
    setCategory("");
  };

  React.useEffect(() => {
    if (scrollToId && rowRefs.current[scrollToId]) {
      rowRefs.current[scrollToId].scrollIntoView({ behavior: 'smooth', block: 'center' });
      setScrollToId(null);
    }
  }, [scrollToId]);

  // 編集
  const handleEdit = (product) => {
    setEditId(product.id);
    setName(product.name);
    setStock(product.stock);
    setPrice(product.price);
    setOrderUnit(product.orderUnit ?? 1);
    setCostPrice(product.costPrice ?? 0);
    setSellPrice(product.sellPrice ?? 0);
    setCategory(product.category);
    setTimeout(() => {
      if (editFormRef.current) {
        editFormRef.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
      }
    }, 0);
  };

  // 削除
  const handleDelete = (id) => {
    setProducts(products.filter((p) => p.id !== id));
    if (editId === id) handleCancel();
    setProductToDelete(null);
  };

  // 編集キャンセル
  const handleCancel = () => {
    setEditId(null);
    setName("");
    setStock("");
    setPrice("");
    setOrderUnit("");
    setCostPrice("");
    setSellPrice("");
    setCategory("");
  };

  // カテゴリ削除
  const handleDeleteCategory = (categoryToDelete) => {
    setCategories(categories.filter((c) => c !== categoryToDelete));
    if (category === categoryToDelete) setCategory("");
    if (searchCategory === categoryToDelete) setSearchCategory("");
    setCategoryToDelete(null);
  };

  // 商品フィルタ・ソート
  const filteredProducts = products.filter((p) =>
    p.name.toLowerCase().includes((searchTerm || "").toLowerCase()) &&
    (searchCategory ? p.category === searchCategory : true)
  );
  // sortedProductsをuseMemoで生成
  const sortedProducts = React.useMemo(() => {
    const arr = [...filteredProducts];
    if (sortConfig.key !== null) {
      arr.sort((a, b) => {
        const aVal = a[sortConfig.key];
        const bVal = b[sortConfig.key];
        if (typeof aVal === "string") {
          return sortConfig.direction === "asc"
            ? aVal.localeCompare(bVal)
            : bVal.localeCompare(aVal);
        } else {
          return sortConfig.direction === "asc" ? aVal - bVal : bVal - aVal;
        }
      });
    }
    return arr;
  }, [filteredProducts, sortConfig]);

  // ソート関数を復活
  const handleSort = (key) => {
    setSortConfig((prev) => {
      if (prev.key === key) {
        if (prev.direction === "asc") return { key, direction: "desc" };
        if (prev.direction === "desc") return { key: null, direction: null };
      }
      return { key, direction: "asc" };
    });
  };

  // exportToCSVをuseCallbackで生成
  const exportToCSV = React.useCallback(() => {
    const header = ["ID", "商品名", "在庫数", "価格", "カテゴリ"];
    const rows = sortedProducts.map((p) => [p.id, p.name, p.stock, p.price, p.category]);
    const csvContent = [header, ...rows].map((row) => row.join(",")).join("\n");
    // BOMを付与
    const bom = new Uint8Array([0xEF, 0xBB, 0xBF]);
    const blob = new Blob([bom, csvContent], { type: "text/csv;charset=utf-8;" });
    const downloadLink = document.createElement("a");
    downloadLink.href = URL.createObjectURL(blob);
    downloadLink.download = `products.csv`;
    downloadLink.click();
  }, [sortedProducts]);

  // ヘッダーから呼び出せるようrefにセット
  React.useEffect(() => {
    if (exportToCSVRef) exportToCSVRef.current = exportToCSV;
  }, [exportToCSVRef, exportToCSV]);

  // 仕入れ・販売操作
  const handleStockAction = (product, type) => {
    const qty = Number(prompt(`${type === 'in' ? '仕入れ' : '販売'}数を入力してください`));
    if (!qty || qty <= 0) return;
    let newStock = product.stock;
    if (type === 'in') newStock += qty;
    if (type === 'out') {
      if (qty > product.stock) return alert('在庫が足りません');
      newStock -= qty;
    }
    setProducts(products.map(p => p.id === product.id ? { ...p, stock: newStock } : p));
    setHistory([
      ...history,
      {
        id: history.length > 0 ? Math.max(...history.map(h => h.id)) + 1 : 1,
        type,
        productId: product.id,
        name: product.name,
        date: new Date().toISOString().slice(0, 7), // YYYY-MM
        qty,
        price: product.price,
        total: qty * product.price
      }
    ]);
  };

  // 週ごとに履歴を集計
  const getWeekKey = (dateStr) => {
    const d = new Date(dateStr + '-01');
    const weekStart = startOfWeek(d, { weekStartsOn: 1 }); // 月曜始まり
    return format(weekStart, 'yyyy-MM-dd');
  };
  const weekKeys = Array.from(new Set(history.map(h => getWeekKey(h.date)))).sort();
  // 商品ごとの週別販売数
  const weeklySales = products.map(p =>
    weekKeys.map(wk => history.filter(h => h.productId === p.id && h.type === 'out' && getWeekKey(h.date) === wk).reduce((sum, h) => sum + h.qty, 0))
  );
  // 商品ごとの直近4週平均販売数
  const avgSales4w = products.map((p, i) => {
    const sales = weeklySales[i].slice(-4);
    return sales.length ? sales.reduce((a, b) => a + b, 0) / sales.length : 0;
  });
  // 在庫切れ予測（あと何週で在庫0になるか）
  const weeksToOOS = products.map((p, i) => avgSales4w[i] > 0 ? p.stock / avgSales4w[i] : Infinity);
  // 発注アドバイス（2週以内に在庫切れ予測）
  const reorderAdvice = products.filter((p, i) => avgSales4w[i] > 0 && weeksToOOS[i] <= 2);

  // 発注登録モーダルを開く
  const openOrderModal = (product) => {
    setOrderProduct(product);
    setOrderDate(new Date().toISOString().slice(0, 10));
    setExpectedDeliveryDate("");
    setOrderQty(1);
    setShowOrderModal(true);
  };
  // 発注登録処理
  const handleOrderSubmit = (e) => {
    e.preventDefault();
    if (!orderProduct || !orderDate || !expectedDeliveryDate || !orderQty || orderQty <= 0) {
      alert("すべての項目を正しく入力してください。");
      return;
    }
    setOrderHistory([
      ...orderHistory,
      {
        id: orderHistory.length > 0 ? Math.max(...orderHistory.map(o => o.id)) + 1 : 1,
        productId: orderProduct.id,
        name: orderProduct.name,
        orderDate,
        expectedDeliveryDate,
        actualDeliveryDate: null,
        qty: Number(orderQty)
      }
    ]);
    setShowOrderModal(false);
  };
  // 納品登録モーダルを開く
  const openDeliveryModal = (order) => {
    setDeliveryOrder(order);
    setActualDeliveryDate(new Date().toISOString().slice(0, 10));
    setShowDeliveryModal(true);
  };
  // 納品登録処理
  const handleDeliverySubmit = (e) => {
    e.preventDefault();
    setOrderHistory(orderHistory.map(o =>
      o.id === deliveryOrder.id ? { ...o, actualDeliveryDate } : o
    ));
    // 在庫を加算
    setProducts(products.map(p =>
      p.id === deliveryOrder.productId ? { ...p, stock: p.stock + deliveryOrder.qty } : p
    ));
    setShowDeliveryModal(false);
  };

  return (
    <div style={{ display: 'flex', minHeight: '100vh', background: 'transparent', transition: 'background 0.4s' }}>
      {/* サイドバー */}
      <div style={{ width: 260, background: darkMode ? 'rgba(30,34,44,0.98)' : 'rgba(255,255,255,0.95)', padding: 28, borderRight: darkMode ? '1px solid #23273a' : '1px solid #e0e4ef', display: 'flex', flexDirection: 'column', justifyContent: 'flex-start', boxShadow: darkMode ? '2px 0 16px #10131a44' : '2px 0 16px #b0b8d844', zIndex: 10, position: 'relative', minHeight: '100vh', transition: 'background 0.4s, color 0.4s' }}>
        <h2 style={{ marginTop: 0, marginBottom: 24, color: darkMode ? '#fff' : '#222', letterSpacing: 2, fontWeight: 800, fontSize: 22, textAlign: 'center', textShadow: darkMode ? '0 2px 8px #0006' : '0 2px 8px #fff8', transition: 'color 0.4s' }}>INVENTORY</h2>
        {/* データセット切替セクション */}
        <div style={{ marginBottom: 32 }}>
          <label style={{ color: darkMode ? '#b0b8d8' : '#555', fontWeight: 600, fontSize: 14, marginBottom: 6, display: 'block', transition: 'color 0.4s' }}>データセット切替</label>
          <select value={currentDataset} onChange={e => setCurrentDataset(e.target.value)} style={{ width: '100%', padding: 8, borderRadius: 8, border: darkMode ? '1px solid #23273a' : '1px solid #b0b8d8', background: darkMode ? '#23273a' : '#f4f7fb', color: darkMode ? '#fff' : '#222', fontWeight: 600, marginBottom: 8, boxShadow: '0 1px 4px #0001', transition: 'background 0.4s, color 0.4s, border 0.4s' }}>
            {datasets.map(ds => (
              <option key={ds} value={ds}>{ds}</option>
            ))}
          </select>
          <input
            type="text"
            placeholder="新しいデータセット名"
            value={newDatasetName}
            onChange={e => setNewDatasetName(e.target.value)}
            style={{ width: '100%', padding: 8, borderRadius: 8, border: darkMode ? '1px solid #23273a' : '1px solid #b0b8d8', background: darkMode ? '#23273a' : '#f4f7fb', color: darkMode ? '#fff' : '#222', fontWeight: 600, marginBottom: 6, boxShadow: '0 1px 4px #0001', transition: 'background 0.4s, color 0.4s, border 0.4s' }}
          />
          <button
            type="button"
            onClick={() => {
              const name = newDatasetName.trim();
              if (!name) return;
              if (datasets.includes(name)) {
                alert('同じ名前のデータセットがあります');
                return;
              }
              setDatasets([...datasets, name]);
              setCurrentDataset(name);
              setNewDatasetName("");
            }}
            style={{ width: '100%', padding: 8, borderRadius: 8, background: 'linear-gradient(90deg, #1976d2 60%, #4fc3f7 100%)', color: '#fff', border: 'none', fontWeight: 700, letterSpacing: 1, boxShadow: '0 2px 8px #1976d233', transition: 'background 0.2s, color 0.4s' }}
          >追加</button>
          {datasets.length > 1 && datasets.map(ds => ds !== currentDataset && (
            <div key={ds} style={{ display: 'flex', alignItems: 'center', marginBottom: 6, background: darkMode ? '#23273a' : '#f4f7fb', borderRadius: 8, padding: '4px 8px', transition: 'background 0.4s' }}>
              <span style={{ flex: 1, color: darkMode ? '#b0b8d8' : '#555', fontWeight: 600, transition: 'color 0.4s' }}>{ds}</span>
              <button
                onClick={() => {
                  if (window.confirm(`「${ds}」を削除しますか？`)) {
                    setDatasets(datasets.filter(d => d !== ds));
                    localStorage.removeItem(`products_${username}_${ds}`);
                    localStorage.removeItem(`history_${username}_${ds}`);
                    if (currentDataset === ds) setCurrentDataset(datasets.find(d => d !== ds) || "メイン");
                  }
                }}
                style={{ background: 'linear-gradient(90deg, #d32f2f 60%, #ff5252 100%)', color: '#fff', border: 'none', borderRadius: 6, padding: '2px 10px', marginLeft: 6, fontWeight: 700, boxShadow: '0 1px 4px #d32f2f22', cursor: 'pointer', transition: 'background 0.2s, color 0.4s' }}
              >削除</button>
            </div>
          ))}
        </div>
        {/* ダークモード切り替えトグル */}
        <button
          onClick={() => {
            if (typeof window !== 'undefined' && window.localStorage) {
              window.localStorage.setItem('darkMode', !darkMode);
            }
            if (typeof window !== 'undefined') {
              window.location.reload();
            }
          }}
          style={{
            position: 'fixed',
            left: 28,
            bottom: 32,
            width: '204px',
            padding: 10,
            borderRadius: 8,
            background: darkMode ? 'linear-gradient(90deg, #23273a 60%, #4fc3f7 100%)' : 'linear-gradient(90deg, #e3e9f0 60%, #1976d2 100%)',
            color: darkMode ? '#fff' : '#222',
            border: 'none',
            fontWeight: 700,
            fontSize: 15,
            letterSpacing: 1,
            boxShadow: '0 2px 8px #1976d233',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            gap: 8,
            transition: 'background 0.4s, color 0.4s',
            zIndex: 100,
          }}
        >
          {darkMode ? (
            <span style={{ fontSize: 18, marginRight: 4 }}>🌙</span>
          ) : (
            <span style={{ fontSize: 18, marginRight: 4 }}>☀️</span>
          )}
          {darkMode ? 'ライトモードに切替' : 'ダークモードに切替'}
        </button>
      </div>
      {/* メインコンテンツ */}
      <div style={{ flex: 1, padding: '40px 32px', maxWidth: 1000, margin: "0 auto", background: darkMode ? 'rgba(24,28,36,0.98)' : 'rgba(255,255,255,0.98)', borderRadius: 24, boxShadow: darkMode ? '0 4px 32px #10131a66' : '0 4px 32px #b0b8d866', marginTop: 32, marginBottom: 32, transition: 'background 0.4s, color 0.4s' }}>
        <h1 style={{ color: darkMode ? '#fff' : '#222', fontWeight: 900, letterSpacing: 2, fontSize: 32, marginBottom: 24, textShadow: darkMode ? '0 2px 8px #0006' : '0 2px 8px #fff8', transition: 'color 0.4s' }}>在庫管理システム</h1>
        {/* 発注アドバイス */}
        {reorderAdvice.length > 0 && (
          <div style={{ background: darkMode ? '#2d2f36' : '#f9fbe7', color: darkMode ? '#ffe082' : '#d84315', padding: 12, borderRadius: 8, marginBottom: 20, fontWeight: 'bold', transition: 'background 0.4s, color 0.4s' }}>
            <span>発注アドバイス：</span>
            {reorderAdvice.map((p) => (
              <span key={p.id} style={{ marginRight: 16 }}>{`「${p.name}」は約${Math.ceil(weeksToOOS[products.indexOf(p)])}週で在庫切れ予測`}</span>
            ))}
          </div>
        )}
        {/* 在庫管理セクション */}
        <section style={{ marginBottom: 48 }}>
          <h2 style={{ color: darkMode ? '#fff' : '#222', fontWeight: 800, fontSize: 22, marginBottom: 16, transition: 'color 0.4s' }}>在庫管理</h2>
          <div style={{ marginBottom: 16 }}>
            <select onChange={(e) => setSearchCategory(e.target.value)} value={searchCategory} style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, transition: 'background 0.4s, color 0.4s, border 0.4s' }}>
              <option value="">全カテゴリ</option>
              {categories.map((c) => (
                <option key={c} value={c}>{c}</option>
              ))}
            </select>
          </div>
          <div style={{ marginBottom: 20 }}>
            <h3 style={{ color: darkMode ? '#fff' : '#222', fontWeight: 700, fontSize: 18, marginBottom: 8, transition: 'color 0.4s' }}>カテゴリを追加</h3>
            <input
              type="text"
              placeholder="新しいカテゴリ"
              value={newCategory}
              onChange={(e) => setNewCategory(e.target.value)}
              style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, marginRight: 8, transition: 'background 0.4s, color 0.4s, border 0.4s' }}
            />
            <button
              type="button"
              onClick={() => {
                if (newCategory.trim() !== "" && !categories.includes(newCategory.trim())) {
                  setCategories([...categories, newCategory.trim()]);
                  setNewCategory("");
                }
              }}
              style={{ background: darkMode ? '#1976d2' : '#1976d2', color: '#fff', border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}
            >追加</button>
            {newCategory && (
              <button onClick={() => setNewCategory("")} style={{ marginLeft: 8, background: darkMode ? '#444' : '#aaa', color: '#fff', border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>
                キャンセル
              </button>
            )}
            <div style={{ marginTop: 12 }}>
              {categories.map((c) => (
                <span key={c} style={{ display: "inline-block", marginRight: 8, background: darkMode ? '#23273a' : '#f0f0f0', color: darkMode ? '#fff' : '#222', padding: "2px 8px", borderRadius: 4, transition: 'background 0.4s, color 0.4s' }}>
                  {c}
                  <button onClick={() => setCategoryToDelete(c)} style={{ marginLeft: 4, color: darkMode ? '#fff' : 'black', border: "none", background: "transparent", cursor: "pointer" }}>×</button>
                </span>
              ))}
            </div>
            {/* カテゴリ削除確認ダイアログ */}
            {categoryToDelete && (
              <div style={{ position: "fixed", top: 0, left: 0, width: "100vw", height: "100vh", background: darkMode ? 'rgba(0,0,0,0.5)' : 'rgba(0,0,0,0.3)', display: "flex", alignItems: "center", justifyContent: "center", zIndex: 1000 }}>
                <div style={{ background: darkMode ? '#23273a' : 'white', color: darkMode ? '#fff' : '#222', padding: 24, borderRadius: 8, boxShadow: "0 2px 8px rgba(0,0,0,0.2)", transition: 'background 0.4s, color 0.4s' }}>
                  <p>「{categoryToDelete}」を削除しますか？</p>
                  <button onClick={() => handleDeleteCategory(categoryToDelete)} style={{ marginRight: 12, background: darkMode ? '#d32f2f' : '#d32f2f', color: '#fff', border: "none", padding: "6px 16px", borderRadius: 4, fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>はい</button>
                  <button onClick={() => setCategoryToDelete(null)} style={{ background: darkMode ? '#444' : '#aaa', color: '#fff', border: "none", padding: "6px 16px", borderRadius: 4, fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>いいえ</button>
                </div>
              </div>
            )}
          </div>
          <form ref={editFormRef} onSubmit={handleSubmit} style={{ marginBottom: 20 }}>
            <input
              type="text"
              placeholder="商品名"
              value={name}
              onChange={(e) => setName(e.target.value)}
              style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, marginRight: 8, marginBottom: 8, transition: 'background 0.4s, color 0.4s, border 0.4s' }}
            />
            <input
              type="number"
              placeholder="在庫数"
              value={stock}
              onChange={(e) => setStock(e.target.value)}
              style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, marginRight: 8, marginBottom: 8, transition: 'background 0.4s, color 0.4s, border 0.4s' }}
            />
            <input
              type="number"
              placeholder="価格"
              value={price}
              onChange={(e) => setPrice(e.target.value)}
              style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, marginRight: 8, marginBottom: 8, transition: 'background 0.4s, color 0.4s, border 0.4s' }}
            />
            <input
              type="number"
              placeholder="入数"
              value={orderUnit}
              min={1}
              onChange={(e) => setOrderUnit(e.target.value)}
              style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, marginRight: 8, marginBottom: 8, transition: 'background 0.4s, color 0.4s, border 0.4s' }}
            />
            <input
              type="number"
              placeholder="原価"
              value={costPrice}
              min={0}
              onChange={(e) => setCostPrice(e.target.value)}
              style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, marginRight: 8, marginBottom: 8, transition: 'background 0.4s, color 0.4s, border 0.4s' }}
            />
            <input
              type="number"
              placeholder="売価"
              value={sellPrice}
              min={0}
              onChange={(e) => setSellPrice(e.target.value)}
              style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, marginRight: 8, marginBottom: 8, transition: 'background 0.4s, color 0.4s, border 0.4s' }}
            />
            <select value={category} onChange={(e) => setCategory(e.target.value)} style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, marginRight: 8, marginBottom: 8, transition: 'background 0.4s, color 0.4s, border 0.4s' }}>
              <option value="">カテゴリを選択</option>
              {categories.map((c) => (
                <option key={c} value={c}>{c}</option>
              ))}
            </select>
            <button type="submit" style={{ background: darkMode ? '#1976d2' : '#1976d2', color: '#fff', border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, marginRight: 8, marginBottom: 8, transition: 'background 0.4s, color 0.4s' }}>{editId === null ? "追加" : "更新"}</button>
            {editId !== null && (
              <button type="button" onClick={handleCancel} style={{ marginLeft: 8, background: darkMode ? '#444' : '#aaa', color: '#fff', border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, marginBottom: 8, transition: 'background 0.4s, color 0.4s' }}>
                キャンセル
              </button>
            )}
          </form>
          <table border="1" cellPadding="8" style={{ width: "100%", background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', borderCollapse: 'collapse', border: darkMode ? '1px solid #444' : '1px solid #ccc', transition: 'background 0.4s, color 0.4s, border 0.4s' }}>
            <thead>
              <tr style={{ background: darkMode ? '#181c24' : '#f0f0f0', color: darkMode ? '#fff' : '#222', transition: 'background 0.4s, color 0.4s' }}>
                <th onClick={() => handleSort("id")} style={{ cursor: 'pointer' }}>ID</th>
                <th onClick={() => handleSort("name")} style={{ cursor: 'pointer' }}>商品名</th>
                <th onClick={() => handleSort("stock")} style={{ cursor: 'pointer' }}>在庫数</th>
                <th onClick={() => handleSort("price")} style={{ cursor: 'pointer' }}>価格</th>
                <th>入数</th>
                <th>原価</th>
                <th>売価</th>
                <th>利益高</th>
                <th onClick={() => handleSort("category")} style={{ cursor: 'pointer' }}>カテゴリ</th>
                <th>操作</th>
              </tr>
            </thead>
            <tbody>
              {sortedProducts.map((p) => (
                <tr key={p.id} ref={el => rowRefs.current[p.id] = el} style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', transition: 'background 0.4s, color 0.4s' }}>
                  <td>{p.id}</td>
                  <td>{p.name}</td>
                  <td style={{ color: p.stock <= 3 ? (darkMode ? '#ff5252' : 'red') : (darkMode ? '#fff' : 'black') }}>{p.stock}</td>
                  <td>{p.price}円</td>
                  <td>{p.orderUnit}</td>
                  <td>{p.costPrice}円</td>
                  <td>{p.sellPrice}円</td>
                  <td>{(p.sellPrice - p.costPrice) * p.stock}円</td>
                  <td>{p.category}</td>
                  <td>
                    <button onClick={() => handleEdit(p)} style={{ background: darkMode ? '#1976d2' : '#1976d2', color: '#fff', border: 'none', borderRadius: 8, padding: '4px 10px', fontWeight: 700, marginRight: 4, transition: 'background 0.4s, color 0.4s' }}>編集</button>
                    <button onClick={() => setProductToDelete(p.id)} style={{ background: darkMode ? '#d32f2f' : '#d32f2f', color: '#fff', border: 'none', borderRadius: 8, padding: '4px 10px', fontWeight: 700, marginRight: 4, transition: 'background 0.4s, color 0.4s' }}>削除</button>
                    <button onClick={() => handleStockAction(p, 'in')} style={{ background: darkMode ? '#388e3c' : '#388e3c', color: '#fff', border: 'none', borderRadius: 8, padding: '4px 10px', fontWeight: 700, marginRight: 4, transition: 'background 0.4s, color 0.4s' }}>仕入れ</button>
                    <button onClick={() => handleStockAction(p, 'out')} style={{ background: darkMode ? '#ffa000' : '#ffa000', color: '#fff', border: 'none', borderRadius: 8, padding: '4px 10px', fontWeight: 700, marginRight: 4, transition: 'background 0.4s, color 0.4s' }}>販売</button>
                    <button onClick={() => openOrderModal(p)} style={{ background: darkMode ? '#1976d2' : '#1976d2', color: '#fff', border: 'none', borderRadius: 8, padding: '4px 10px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>発注</button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          {/* 商品削除確認ダイアログ */}
          {productToDelete !== null && (
            <div style={{ position: "fixed", top: 0, left: 0, width: "100vw", height: "100vh", background: darkMode ? 'rgba(0,0,0,0.3)' : 'rgba(0,0,0,0.3)', display: "flex", alignItems: "center", justifyContent: "center", zIndex: 1000 }}>
              <div style={{ background: darkMode ? '#222' : 'white', padding: 24, borderRadius: 8, boxShadow: "0 2px 8px rgba(0,0,0,0.2)" }}>
                <p>この商品を削除しますか？</p>
                <button onClick={() => handleDelete(productToDelete)} style={{ marginRight: 12, background: darkMode ? '#d32f2f' : '#d32f2f', color: darkMode ? '#fff' : 'white', border: "none", padding: "6px 16px", borderRadius: 4 }}>はい</button>
                <button onClick={() => setProductToDelete(null)} style={{ background: darkMode ? '#aaa' : '#aaa', color: darkMode ? '#fff' : 'white', border: "none", padding: "6px 16px", borderRadius: 4 }}>いいえ</button>
              </div>
            </div>
          )}
        </section>
        {/* 発注履歴・リードタイム管理セクション */}
        <section>
          <h2>発注履歴・リードタイム管理</h2>
          <table border="1" cellPadding="6" style={{ width: '100%' }}>
            <thead>
              <tr>
                <th>商品名</th>
                <th>発注日</th>
                <th>納品予定日</th>
                <th>実際の納品日</th>
                <th>数量</th>
                <th>リードタイム(日)</th>
              </tr>
            </thead>
            <tbody>
              {orderHistory.length === 0 && (
                <tr><td colSpan="6" style={{ textAlign: 'center' }}>発注履歴はありません</td></tr>
              )}
              {orderHistory.map((o) => (
                <tr key={o.id}>
                  <td>{o.name}</td>
                  <td>{o.orderDate}</td>
                  <td>{o.expectedDeliveryDate}</td>
                  <td>
                    {o.actualDeliveryDate ? (
                      o.actualDeliveryDate
                    ) : (
                      <button onClick={() => openDeliveryModal(o)}>納品登録</button>
                    )}
                  </td>
                  <td>{o.qty}</td>
                  <td>
                    {o.actualDeliveryDate
                      ? ((new Date(o.actualDeliveryDate) - new Date(o.orderDate)) / (1000*60*60*24))
                      : '-'}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </section>
        {/* 発注登録モーダル */}
        {showOrderModal && (
          <div style={{ position: "fixed", top: 0, left: 0, width: "100vw", height: "100vh", background: darkMode ? 'rgba(0,0,0,0.5)' : 'rgba(0,0,0,0.3)', display: "flex", alignItems: "center", justifyContent: "center", zIndex: 1000 }}>
            <div style={{ background: darkMode ? '#23273a' : 'white', color: darkMode ? '#fff' : '#222', padding: 24, borderRadius: 8, boxShadow: "0 2px 8px rgba(0,0,0,0.2)", transition: 'background 0.4s, color 0.4s' }}>
              <h3 style={{ color: darkMode ? '#fff' : '#222', fontWeight: 700, fontSize: 20, marginBottom: 16, transition: 'color 0.4s' }}>発注登録</h3>
              <form onSubmit={handleOrderSubmit}>
                <div style={{ marginBottom: 8, color: darkMode ? '#fff' : '#222', transition: 'color 0.4s' }}>商品名: <b>{orderProduct?.name}</b></div>
                <div style={{ marginBottom: 8, color: darkMode ? '#fff' : '#222', transition: 'color 0.4s' }}>
                  発注日: <input type="date" value={orderDate} onChange={e => setOrderDate(e.target.value)} required style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, transition: 'background 0.4s, color 0.4s, border 0.4s' }} />
                </div>
                <div style={{ marginBottom: 8, color: darkMode ? '#fff' : '#222', transition: 'color 0.4s' }}>
                  納品予定日: <input type="date" value={expectedDeliveryDate} onChange={e => setExpectedDeliveryDate(e.target.value)} required style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, transition: 'background 0.4s, color 0.4s, border 0.4s' }} />
                </div>
                <div style={{ marginBottom: 8, color: darkMode ? '#fff' : '#222', transition: 'color 0.4s' }}>
                  数量: <input type="number" value={orderQty} min={1} onChange={e => setOrderQty(e.target.value)} required style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, transition: 'background 0.4s, color 0.4s, border 0.4s' }} />
                </div>
                <button type="submit" style={{ marginRight: 12, background: darkMode ? '#1976d2' : '#1976d2', color: '#fff', border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>登録</button>
                <button type="button" onClick={() => setShowOrderModal(false)} style={{ background: darkMode ? '#444' : '#aaa', color: '#fff', border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>キャンセル</button>
              </form>
            </div>
          </div>
        )}
        {/* 納品登録モーダル */}
        {showDeliveryModal && (
          <div style={{ position: "fixed", top: 0, left: 0, width: "100vw", height: "100vh", background: darkMode ? 'rgba(0,0,0,0.5)' : 'rgba(0,0,0,0.3)', display: "flex", alignItems: "center", justifyContent: "center", zIndex: 1000 }}>
            <div style={{ background: darkMode ? '#23273a' : 'white', color: darkMode ? '#fff' : '#222', padding: 24, borderRadius: 8, boxShadow: "0 2px 8px rgba(0,0,0,0.2)", transition: 'background 0.4s, color 0.4s' }}>
              <h3 style={{ color: darkMode ? '#fff' : '#222', fontWeight: 700, fontSize: 20, marginBottom: 16, transition: 'color 0.4s' }}>納品登録</h3>
              <form onSubmit={handleDeliverySubmit}>
                <div style={{ marginBottom: 8, color: darkMode ? '#fff' : '#222', transition: 'color 0.4s' }}>商品名: <b>{deliveryOrder?.name}</b></div>
                <div style={{ marginBottom: 8, color: darkMode ? '#fff' : '#222', transition: 'color 0.4s' }}>発注日: {deliveryOrder?.orderDate}</div>
                <div style={{ marginBottom: 8, color: darkMode ? '#fff' : '#222', transition: 'color 0.4s' }}>納品予定日: {deliveryOrder?.expectedDeliveryDate}</div>
                <div style={{ marginBottom: 8, color: darkMode ? '#fff' : '#222', transition: 'color 0.4s' }}>
                  実際の納品日: <input type="date" value={actualDeliveryDate} onChange={e => setActualDeliveryDate(e.target.value)} required style={{ background: darkMode ? '#23273a' : '#fff', color: darkMode ? '#fff' : '#222', border: darkMode ? '1px solid #444' : '1px solid #ccc', borderRadius: 8, padding: 6, transition: 'background 0.4s, color 0.4s, border 0.4s' }} />
                </div>
                <button type="submit" style={{ marginRight: 12, background: darkMode ? '#1976d2' : '#1976d2', color: '#fff', border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>登録</button>
                <button type="button" onClick={() => setShowDeliveryModal(false)} style={{ background: darkMode ? '#444' : '#aaa', color: '#fff', border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 700, transition: 'background 0.4s, color 0.4s' }}>キャンセル</button>
              </form>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default Home; 