import React, { useState, useEffect } from "react";
import { HashRouter, Routes, Route, Navigate, Link } from "react-router-dom";
import Home from "./pages/Home";
import Login from "./pages/Login";
import Help from "./pages/Help";
import Analysis from "./pages/Analysis";
import "./App.css";

function AIChat({ products, hideInput }) {
  const [input, setInput] = React.useState("");
  const [messages, setMessages] = React.useState([
    { role: "system", text: "在庫管理AIチャットへようこそ！\n\n【できること例】\n・商品名や在庫数、価格、原価、利益率などの質問\n・在庫が多い/少ない商品一覧\n・利益率が高い/低い商品\n・カテゴリごとの在庫合計や平均価格\n・発注や納品、原価の確認\n・全商品の在庫リストや売価順の一覧\n・おすすめ商品の提案\n・2商品の比較\n・納品履歴の案内\n・「ありがとう」などへの雑談応答\n\n上記以外にも、業務に関する質問に幅広く対応します。お気軽にご質問ください。" }
  ]);
  const handleSend = () => {
    if (!input.trim()) return;
    const userMsg = { role: "user", text: input };
    let aiMsg = { role: "ai", text: "申し訳ありません、よく分かりませんでした。" };
    // 商品名で自動検索
    const found = products.find(p => p.name && input.includes(p.name));
    if (found) {
      aiMsg = { role: "ai", text: `「${found.name}」の在庫は${found.stock}、価格は${found.price}円、カテゴリは${found.category}です。` };
    } else if (/在庫(が|は)?少ない|在庫不足|在庫危険/.test(input)) {
      const low = products.filter(p => p.stock <= 3);
      aiMsg = { role: "ai", text: low.length ? low.map(p => `「${p.name}」在庫${p.stock}`).join("、") + " があります。" : "在庫が少ない商品はありません。" };
    } else if (/在庫(が|は)?多い|在庫豊富|在庫余裕/.test(input)) {
      const high = products.filter(p => p.stock >= 10);
      aiMsg = { role: "ai", text: high.length ? high.map(p => `「${p.name}」在庫${p.stock}`).join("、") + " があります。" : "在庫が多い商品はありません。" };
    } else if (/利益率(が|は)?高い|利益率順|儲け率/.test(input)) {
      const sorted = [...products].sort((a, b) => ((b.sellPrice-b.costPrice)/b.costPrice - (a.sellPrice-a.costPrice)/a.costPrice));
      aiMsg = { role: "ai", text: sorted.length ? `利益率が高い順: ${sorted.map(p => `「${p.name}」(${(((p.sellPrice-p.costPrice)/p.costPrice)*100).toFixed(1)}%)`).join("、")}` : "商品がありません。" };
    } else if (/利益率(が|は)?低い|損|赤字/.test(input)) {
      const sorted = [...products].sort((a, b) => ((a.sellPrice-a.costPrice)/a.costPrice - (b.sellPrice-b.costPrice)/b.costPrice));
      aiMsg = { role: "ai", text: sorted.length ? `利益率が低い順: ${sorted.map(p => `「${p.name}」(${(((p.sellPrice-p.costPrice)/p.costPrice)*100).toFixed(1)}%)`).join("、")}` : "商品がありません。" };
    } else if (/カテゴリ(別|ごと|毎)|カテゴリ一覧/.test(input)) {
      const cats = {};
      products.forEach(p => { cats[p.category] = (cats[p.category]||0) + p.stock; });
      aiMsg = { role: "ai", text: Object.keys(cats).length ? Object.entries(cats).map(([cat, sum]) => `${cat}: 在庫合計${sum}`).join("、") : "カテゴリ情報がありません。" };
    } else if (/平均価格|平均単価/.test(input)) {
      const avg = products.length ? (products.reduce((sum, p) => sum + p.price, 0) / products.length).toFixed(1) : 0;
      aiMsg = { role: "ai", text: `全商品の平均価格は${avg}円です。` };
    } else if (/発注|注文/.test(input)) {
      const match = products.find(p => input.includes(p.name));
      aiMsg = { role: "ai", text: match ? `「${match.name}」を発注しますか？発注数や納期も指定できます。` : "発注したい商品名を教えてください。" };
    } else if (/原価/.test(input)) {
      const match = products.find(p => input.includes(p.name));
      aiMsg = { role: "ai", text: match ? `「${match.name}」の原価は${match.costPrice}円です。` : "原価を知りたい商品名を教えてください。" };
    } else if (/全商品|在庫一覧|リスト/.test(input)) {
      aiMsg = { role: "ai", text: products.length ? products.map(p => `「${p.name}」在庫${p.stock}`).join("、") : "商品がありません。" };
    } else if (/売価(が|は)?高い|高額順|高い順/.test(input)) {
      const sorted = [...products].sort((a, b) => b.sellPrice - a.sellPrice);
      aiMsg = { role: "ai", text: sorted.length ? `売価が高い順: ${sorted.map(p => `「${p.name}」(${p.sellPrice}円)`).join("、")}` : "商品がありません。" };
    } else if (/おすすめ|今日のおすすめ/.test(input)) {
      const pick = products[Math.floor(Math.random()*products.length)];
      aiMsg = { role: "ai", text: pick ? `今日のおすすめは「${pick.name}」です！在庫${pick.stock}、売価${pick.sellPrice}円。` : "おすすめできる商品がありません。" };
    } else if (/違い|比較/.test(input)) {
      const names = products.filter(p => input.includes(p.name));
      if (names.length === 2) {
        const [a, b] = names;
        aiMsg = { role: "ai", text: `「${a.name}」と「${b.name}」の比較：在庫${a.stock} vs ${b.stock}、売価${a.sellPrice}円 vs ${b.sellPrice}円、原価${a.costPrice}円 vs ${b.costPrice}円。` };
      } else {
        aiMsg = { role: "ai", text: "比較したい2つの商品名を入力してください。" };
      }
    } else if (/納品履歴|履歴/.test(input)) {
      aiMsg = { role: "ai", text: "納品履歴は分析ページでご確認いただけます。" };
    } else if (/ありがとう|助かる|感謝/.test(input)) {
      aiMsg = { role: "ai", text: "どういたしまして！他にも何かあればご質問ください。" };
    }
    setMessages(msgs => [...msgs, userMsg, aiMsg]);
    setInput("");
  };
  return (
    <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
      <div style={{ flex: 1, overflowY: 'auto', background: '#f5f5f5', padding: 8, borderRadius: 6, fontSize: '0.95em', marginBottom: 0 }}>
        {messages.map((m, i) => (
          <div key={i} style={{ margin: '6px 0', color: m.role === 'user' ? '#1976d2' : '#333', textAlign: m.role === 'user' ? 'right' : 'left' }}>
            {m.role === 'system' ? <span style={{ color: '#888' }}>{m.text}</span> : m.text}
          </div>
        ))}
      </div>
      {!hideInput && (
        <div style={{ display: 'flex', padding: '8px 0 0 0', background: 'transparent' }}>
          <input
            type="text"
            value={input}
            onChange={e => setInput(e.target.value)}
            onKeyDown={e => { if (e.key === 'Enter') handleSend(); }}
            placeholder="AIに質問..."
            style={{ flex: 1, padding: 8, borderRadius: 4, border: '1px solid #ccc', fontSize: '1em', marginRight: 8 }}
          />
          <button
            onClick={handleSend}
            style={{ minWidth: 80, padding: '8px 0', borderRadius: 4, background: '#1976d2', color: '#fff', border: 'none', fontWeight: 'bold', fontSize: '1em' }}
          >送信</button>
        </div>
      )}
    </div>
  );
}

function AppContent(props) {
  const { darkMode, showHeader, setShowHeader, showSidebar, setShowSidebar, searchTerm, setSearchTerm, importFromCSVRef, exportToCSVRef, username, setUsername, handleLogout, products, setProducts, history, setHistory } = props;
  const [showAIChat, setShowAIChat] = React.useState(false);
  // 左上に再表示ボタン
  const showSidebarButton = !showSidebar;
  const showHeaderButton = !showHeader;
  // 画面幅でスマホ判定
  const isMobile = window.innerWidth <= 600;
  return (
    <div style={{ display: 'flex', minHeight: '100vh' }}>
      {/* サイドバー */}
      {showSidebar && (
        <div className="sidebar" style={{ background: darkMode ? '#222' : '#f4f4f4', borderRight: '1px solid #ccc', display: 'flex', flexDirection: 'column', position: 'relative' }}>
          <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', padding: '12px 8px 8px 8px', borderBottom: '1px solid #ccc', fontWeight: 'bold', fontSize: '1.2em', color: darkMode ? '#fff' : '#333', position: 'relative' }}>
            <span style={{ marginBottom: 6 }}>機能の切り替え</span>
            <button
              onClick={() => setShowSidebar(false)}
              style={{
                background: 'none',
                border: 'none',
                color: darkMode ? '#fff' : '#333',
                fontSize: '1.7em',
                cursor: 'pointer',
                margin: 0,
                zIndex: 6000
              }}
              aria-label="サイドバーを閉じる"
              title="サイドバーを閉じる"
            >←</button>
          </div>
          <nav style={{ flex: 'none', padding: 16 }}>
            <Link to="/home" style={{ display: 'block', marginBottom: 12, color: location.pathname === '/home' ? '#1976d2' : (darkMode ? '#fff' : '#222'), fontWeight: location.pathname === '/home' ? 'bold' : 'normal', textDecoration: 'none' }}>在庫管理</Link>
            <Link to="/analysis" style={{ display: 'block', marginBottom: 12, color: location.pathname === '/analysis' ? '#1976d2' : (darkMode ? '#fff' : '#222'), fontWeight: location.pathname === '/analysis' ? 'bold' : 'normal', textDecoration: 'none' }}>分析</Link>
            <Link to="/help" style={{ display: 'block', marginBottom: 12, color: location.pathname === '/help' ? '#1976d2' : (darkMode ? '#fff' : '#222'), fontWeight: location.pathname === '/help' ? 'bold' : 'normal', textDecoration: 'none' }}>使い方ガイド</Link>
          </nav>
          <div style={{ flex: 1 }} />
        </div>
      )}
      {/* メインコンテンツ */}
      <div style={{ flex: 1, minWidth: 0, background: darkMode ? '#181818' : '#fff', position: 'relative' }}>
        {/* スマホ時サイドバーオーバーレイ */}
        {isMobile && showSidebar && (
          <div
            onClick={() => setShowSidebar(false)}
            style={{
              position: 'fixed',
              left: 0, top: 0, right: 0, bottom: 0,
              background: 'rgba(0,0,0,0.25)',
              zIndex: 4000
            }}
          />
        )}
        {/* ヘッダー */}
        {showHeader && (
          <div className="header" style={{ display: 'flex', alignItems: 'center', background: darkMode ? '#181818' : '#fff', borderBottom: '1px solid #ccc', position: 'sticky', top: 0, zIndex: 100 }}>
            <input
              type="text"
              placeholder="商品名で検索"
              value={searchTerm}
              onChange={e => setSearchTerm(e.target.value)}
              style={{ marginLeft: 16, padding: 4, borderRadius: 4, border: '1px solid #ccc', width: 180 }}
            />
            <label style={{ marginLeft: 8, marginRight: 0 }}>
              <input type="file" accept=".csv" style={{ display: 'none' }} onChange={e => importFromCSVRef.current && importFromCSVRef.current(e)} />
              <span style={{ padding: '6px 16px', background: '#2196F3', color: '#fff', borderRadius: 4, cursor: 'pointer', fontWeight: 'bold' }}>CSV読み込み</span>
            </label>
            <button
              className="csv-export-btn"
              onClick={() => exportToCSVRef.current && exportToCSVRef.current()}
              style={{
                marginLeft: 8,
                background: "#388e3c",
                color: "#fff",
                border: "none",
                borderRadius: 4,
                padding: "6px 16px",
                fontWeight: "bold",
                cursor: "pointer"
              }}
            >
              CSV出力
            </button>
            <button onClick={() => setShowHeader(v => !v)} style={{ marginLeft: 'auto', marginRight: 16, background: 'none', border: 'none', color: darkMode ? '#fff' : '#333', fontSize: '1.2em', cursor: 'pointer' }} title={showHeader ? 'ヘッダーを隠す' : 'ヘッダーを表示'}>
              {showHeader ? '▲' : '▼'}
            </button>
          </div>
        )}
        <Routes>
          <Route
            path="/"
            element={
              username ? <Navigate to="/home" /> : <Login onLogin={setUsername} />
            }
          />
          <Route
            path="/home"
            element={username ? <Home username={username} onLogout={handleLogout} darkMode={darkMode} searchTerm={searchTerm} exportToCSVRef={exportToCSVRef} products={products} setProducts={setProducts} history={history} setHistory={setHistory} /> : <Navigate to="/" />}
          />
          <Route
            path="/help"
            element={username ? <Help /> : <Navigate to="/" />}
          />
          <Route
            path="/analysis"
            element={username ? <Analysis username={username} darkMode={darkMode} products={products} history={history} /> : <Navigate to="/" />}
          />
          {/* どのパスにもマッチしない場合はルートへ */}
          <Route path="*" element={<Navigate to="/" />} />
        </Routes>
        {/* サイドバー再表示ボタン */}
        {showSidebarButton && (
          <button
            onClick={() => setShowSidebar(true)}
            style={{ position: 'fixed', left: 12, top: 12, zIndex: 4000, background: '#1976d2', color: '#fff', border: 'none', borderRadius: 6, width: 40, height: 40, fontSize: '1.5em', boxShadow: '0 2px 8px rgba(0,0,0,0.2)', cursor: 'pointer' }}
            title="サイドバーを表示"
          >≡</button>
        )}
        {/* ヘッダー再表示ボタン */}
        {showHeaderButton && (
          <button
            onClick={() => setShowHeader(true)}
            style={{ position: 'fixed', left: showSidebarButton ? 60 : 12, top: 12, zIndex: 4000, background: '#1976d2', color: '#fff', border: 'none', borderRadius: 6, width: 40, height: 40, fontSize: '1.3em', boxShadow: '0 2px 8px rgba(0,0,0,0.2)', cursor: 'pointer' }}
            title="ヘッダーを表示"
          >▲</button>
        )}
        {/* 常に右下にAIチャット フローティングボタンを表示 */}
        <>
          <button
            onClick={() => setShowAIChat(v => !v)}
            style={{ position: 'fixed', right: 32, bottom: 32, zIndex: 3000, background: '#1976d2', color: '#fff', border: 'none', borderRadius: '50%', width: 56, height: 56, fontSize: '1.7em', boxShadow: '0 2px 8px rgba(0,0,0,0.2)', cursor: 'pointer' }}
            title="AIチャットを開く"
          >
            💬
          </button>
          {showAIChat && (
            <div style={{ position: 'fixed', right: 32, bottom: 100, zIndex: 3100, width: 340, maxWidth: '90vw', background: darkMode ? '#222' : '#fff', border: '1px solid #888', borderRadius: 12, boxShadow: '0 2px 16px rgba(0,0,0,0.25)', padding: 0, display: 'flex', flexDirection: 'column', height: 420 }}>
              <div style={{ padding: 12, borderBottom: '1px solid #ccc', fontWeight: 'bold', background: darkMode ? '#333' : '#f5f5f5', color: darkMode ? '#fff' : '#333', display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <span>AIチャット</span>
                <button onClick={() => setShowAIChat(false)} style={{ background: 'none', border: 'none', color: darkMode ? '#fff' : '#333', fontSize: '1.2em', cursor: 'pointer' }}>×</button>
              </div>
              <div style={{ flex: 1, minHeight: 0, padding: 8, display: 'flex', flexDirection: 'column' }}>
                <AIChat products={products} />
              </div>
            </div>
          )}
        </>
      </div>
    </div>
  );
}

function App() {
  // localStorageから初期値を取得
  const [username, setUsername] = useState(() => {
    return localStorage.getItem("username") || "";
  });

  const [darkMode] = useState(() => {
    return localStorage.getItem("darkMode") === "true";
  });

  const [showHeader, setShowHeader] = useState(true);
  const [showSidebar, setShowSidebar] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  // CSV出力用のref
  const exportToCSVRef = React.useRef(null);
  // CSV読み込み用のref
  const importFromCSVRef = React.useRef(null);

  // products, historyをAppで管理
  const initialProducts = [
    { id: 1, name: "商品A", stock: 10, price: 1000, orderUnit: 1, costPrice: 700, sellPrice: 1200, category: "食品" },
    { id: 2, name: "商品B", stock: 5, price: 2000, orderUnit: 1, costPrice: 1500, sellPrice: 2500, category: "家電" },
  ];
  const getInitialProducts = (username) => {
    const saved = localStorage.getItem(`products_${username}`);
    if (saved) return JSON.parse(saved);
    return initialProducts;
  };
  const getInitialHistory = (username) => {
    const saved = localStorage.getItem(`history_${username}`);
    if (saved) return JSON.parse(saved);
    return [];
  };
  const [products, setProducts] = useState(() => getInitialProducts(username));
  const [history, setHistory] = useState(() => getInitialHistory(username));

  // productsとhistoryの変更をlocalStorageに保存（アカウントごと）
  useEffect(() => {
    if (username) localStorage.setItem(`products_${username}`, JSON.stringify(products));
  }, [products, username]);
  useEffect(() => {
    if (username) localStorage.setItem(`history_${username}`, JSON.stringify(history));
  }, [history, username]);
  // アカウント切り替え時にデータを再読込
  useEffect(() => {
    setProducts(getInitialProducts(username));
    setHistory(getInitialHistory(username));
  }, [username]);

  useEffect(() => {
    if (username) {
      localStorage.setItem("username", username);
    } else {
      localStorage.removeItem("username");
    }
  }, [username]);

  useEffect(() => {
    document.body.classList.toggle("dark-mode", darkMode);
    localStorage.setItem("darkMode", darkMode);
  }, [darkMode]);

  const handleLogout = () => {
    setUsername("");
  };

  return (
    <HashRouter>
      <AppContent
        darkMode={darkMode}
        showHeader={showHeader}
        setShowHeader={setShowHeader}
        showSidebar={showSidebar}
        setShowSidebar={setShowSidebar}
        searchTerm={searchTerm}
        setSearchTerm={setSearchTerm}
        importFromCSVRef={importFromCSVRef}
        exportToCSVRef={exportToCSVRef}
        username={username}
        setUsername={setUsername}
        handleLogout={handleLogout}
        products={products}
        setProducts={setProducts}
        history={history}
        setHistory={setHistory}
      />
    </HashRouter>
  );
}

export default App;
