import React, { useState } from "react";

function validatePassword(pw) {
  // 英字・数字・特殊文字をすべて含む6文字以上
  return /[a-zA-Z]/.test(pw) && /[0-9]/.test(pw) && /[^a-zA-Z0-9]/.test(pw) && pw.length >= 6;
}

function Login({ onLogin }) {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  // アカウント情報はlocalStorage.accounts(JSON)
  const getAccounts = () => JSON.parse(localStorage.getItem("accounts") || "{}")
  const setAccounts = (obj) => localStorage.setItem("accounts", JSON.stringify(obj));

  const handleLogin = (e) => {
    e.preventDefault();
    const accounts = getAccounts();
    if (!accounts[username]) {
      setError("アカウントが存在しません");
      return;
    }
    if (accounts[username] !== password) {
      setError("パスワードが違います");
      return;
    }
    setError("");
    onLogin(username);
  };

  const handleRegister = () => {
    if (!username || !password) {
      setError("アカウント名とパスワードを入力してください");
      return;
    }
    if (!validatePassword(password)) {
      setError("パスワードは英字・数字・特殊文字を含む6文字以上にしてください");
      return;
    }
    const accounts = getAccounts();
    if (accounts[username]) {
      setError("すでに存在するアカウント名です");
      return;
    }
    accounts[username] = password;
    setAccounts(accounts);
    setError("アカウントを作成しました。ログインしてください。");
  };

  const handleDelete = () => {
    if (!username || !password) {
      setError("アカウント名とパスワードを入力してください");
      return;
    }
    const accounts = getAccounts();
    if (!accounts[username] || accounts[username] !== password) {
      setError("アカウント名またはパスワードが違います");
      return;
    }
    delete accounts[username];
    setAccounts(accounts);
    // 関連データも削除
    localStorage.removeItem(`products_${username}`);
    localStorage.removeItem(`history_${username}`);
    setError("アカウントを削除しました");
  };

  return (
    <div style={{ padding: 24, maxWidth: 400, margin: "0 auto" }}>
      <h2>ログイン</h2>
      <form onSubmit={handleLogin}>
        <input
          type="text"
          placeholder="アカウント名"
          value={username}
          onChange={e => setUsername(e.target.value)}
          style={{ marginBottom: 8, width: "100%" }}
        />
        <input
          type="password"
          placeholder="パスワード"
          value={password}
          onChange={e => setPassword(e.target.value)}
          style={{ marginBottom: 8, width: "100%" }}
        />
        <button type="submit" style={{ width: "100%", marginBottom: 8 }}>ログイン</button>
      </form>
      <button onClick={handleRegister} style={{ width: "100%", marginBottom: 8 }}>新規作成</button>
      <button onClick={handleDelete} style={{ width: "100%", background: "#d32f2f", color: "white" }}>アカウント削除</button>
      {error && <div style={{ color: "red", marginTop: 12 }}>{error}</div>}
    </div>
  );
}

export default Login; 